// options.tap = false

const query = gql`
  {
    markers {
      title
      id
      latitude
      longitude
      description
      slideshow {
        id
        fileName
        size
        url
      }
    }
  }
`;

request(
  "https://api-eu-central-1.graphcms.com/v2/ckihn08el03nl01z2cgsebsif/master",
  query
).then((data) => dataLoaded(data));

var sidebar = document.getElementById("custom-sidebar-123");

function dataLoaded(data) {
  data.markers.forEach(function (item, i) {
    var marker = L.marker([item.latitude, item.longitude], {
      icon: fistIcon
    }).addTo(map);
    //marker.bindPopup(item.title);

    marker.on("click", function () {
      // delete sidebar is it's still open or it's clicked on marker twice
      //sidebar.innerHTML = "</br>";
      sidebar.classList.remove("hidden");
      // create content
      var result = md.render(item.description);
      sidebar.innerHTML +=
        "<div style='position:relative' class='sidebar_container'>" +
        result +
        "</div>";

      console.log(result);

      // create close element
      var closer = document.createElement("div");
      closer.textContent = "";

      closer.id = "closer";
      sidebar.append(closer);

      // add close event to closer
      closer.addEventListener("click", function () {
        console.log("close");

        sidebar.classList.add("hidden");
        sidebar.innerHTML = "";
      });
    });
  });
}
